import React from "react";

export const withCheckedStyles = WrappedComponent => props => {
  const {
    word: { completed }
  } = props; //,      ' word
  const additionalStyles = completed
    ? { textDecoration: "line-through", color: "red" }
    : {};
  return <WrappedComponent {...{ additionalStyles, ...props }} />; //.    props
};
